<template>
  <div>I am in hooks</div>
  <div>{{ x }}-----{{ y }}</div>
</template>

<script>
//mixin -> HOC & render-propsslot -> Hook
//
//
//
//,
// state method 
import { useMousePosition } from '@/hooks/hooks'
export default {
  setup() {
    const { x, y } = useMousePosition()
    // other logic...
    return { x, y }
  },
}
</script>

